// @flow

import React, {useState} from 'react'
import {ThemeProvider} from 'styled-components'
import {CssBaseline} from '@material-ui/core'
import {createMuiTheme} from '@material-ui/core/styles'

import {GlobalStyle, Container, Counter, Overlay} from './styles'

// import {useInterval} from '../../Utils/Common'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

const KegelTraining = () => {
  const [num] = useState<number>(100)

  return (
    <>
      <CssBaseline />
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        <Container>
          <Counter>{num}</Counter>
          <Overlay />
        </Container>
      </ThemeProvider>
    </>
  )
}

export default KegelTraining
